.workspace {
  width: 100%;
  height: 100%;
  position: relative;
  background: #f5f7fb;
  box-shadow: 0px 4px 74px rgba(0, 0, 0, 0.25);
}

.workspaceHeader {
  width: 1098px;
  left: 294px;
  top: 118px;
  position: absolute;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.workspaceTitle {
  color: #0c063a;
  font-size: 24px;
  font-family: Poppins;
  font-weight: 600;
  letter-spacing: 0.48;
  word-wrap: break-word;
}

.workspaceBadge {
  justify-content: flex-start;
  align-items: center;
  gap: 4px;
  display: flex;
}

.workspaceBadgeContainer {
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 4px;
  padding-bottom: 4px;
  background: white;
  border-radius: 8px;
  justify-content: flex-start;
  align-items: center;
  gap: 8px;
  display: flex;
}

.workspaceBadgeIcon {
  width: 21px;
  height: 21px;
  position: relative;
}

.workspaceBadgeDot {
  width: 24px;
  height: 24px;
  position: relative;
}

.workspaceBadgeDotInner {
  width: 8.55px;
  height: 5.03px;
  left: 7.72px;
  top: 9.88px;
  position: absolute;
  background: #0c063a;
}

.workspaceList {
  height: 406px;
  left: 294px;
  top: 230px;
  position: absolute;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 24px;
  display: inline-flex;
}

.recentWorkspace {
  width: 1057px;
  justify-content: space-between;
  align-items: center;
  display: inline-flex;
}

.recentWorkspaceTitle {
  color: black;
  font-size: 24px;
  font-family: Plus Jakarta Sans;
  font-weight: 600;
  word-wrap: break-word;
}

.recentWorkspaceViewAll {
  color: #5d5b73;
}

